import firebase from '../../firebase'
const database = firebase.database();

export const requestFirebase = {
    addOrUpdate: (ref, data) => {
        return new Promise((resolve, reject) => {
            const fref = database.ref()

            let update = {}
            update[`/funcionarios/` + ref] = data

            fref.update(update)
            resolve('Update')
        })
    },
    get: () => {
        return new Promise((resolve, reject) => {
            const fref = database.ref('funcionarios')

            fref.once('value').then(s => {
                let value = s.val()
                resolve(value)
            })
        })
    },
    del: (ref) => {
        return new Promise((resolve, reject) => {
            const fref = database.ref(`/funcionarios/${ref}`)

            fref.remove()
            resolve('Deletado')
        })
    }
}