import React, { useEffect, useCallback } from 'react';
import './App.css';
import ListagemFuncionarios from './components/ListagemFuncionarios'
import ModalCadastro from './components/CadastroFuncionarios/ModalCadastro'

import { requestFirebase } from './util/firebase/RequestFirebase'
import { LocalStorage } from './util/localstorage/LocalStorage'
import { useFuncionarios } from './context/Funcionarios'
import { animationApp } from './animations/animations'

function App() {
    const { setFuncionarios } = useFuncionarios()

    const getFirebase = useCallback(async () => {
        const firebase = await requestFirebase.get()
        const storage = LocalStorage.getLocalStorage('funcionarios')

        if (storage === null || storage !== firebase) {
            LocalStorage.addLocalStorage('funcionarios', firebase)
        }
        setFuncionarios(firebase)

        animationApp('data-title', 'reset')
        animationApp('data-cad', 'reset')
        animationApp('data-lista', 'reset')
    }, [setFuncionarios])


    useEffect(() => {
        getFirebase()
    }, [getFirebase])

    return (
        <section className="App">
            <p className="title" data-title>Cadastro de Funcionarios</p>
            <div data-cad>
                <ModalCadastro />
            </div><br /><br />
            <div data-lista>
                <ListagemFuncionarios />
            </div>
        </section>
    );
}

export default App;