import React, {useState} from 'react';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import { requestFirebase } from '../../util/firebase/RequestFirebase'
import firebase from '../../firebase'
import './style.css'
import { useFuncionarios } from '../../context/Funcionarios';
import validacaoForm, { temp } from '../../util/validacaoForm/validacaoForm'

const useStyles = makeStyles((theme) => ({
    input: { width: '48%' },
    inputSalario: { width: '48%' },
    multiline:{ width: '100%' }
}));

function CadastroFuncionarios({closeModal}) {
    const { funcionarios, setFuncionarios } = useFuncionarios()    
    const [validacao, setValidacao] = useState(temp)
    const classes = useStyles();
    const [ funcionario, setFuncionario ] = useState({
        nome : '',
        sobrenome: '',
        dtNascimento: '',
        cargo: '',
        salario: '',
        descricaoCargo: ''
    })
    

    const addFirebase = async () => {
        if (Object.values(validacaoForm(funcionario)).every(e => e.error === false)) {
            let key = firebase.database().ref().child('funcionarios').push().key
            await requestFirebase.addOrUpdate(key, funcionario)
            setFuncionarios({ ...funcionarios, [key]: funcionario })   
            setValidacao({ ...temp })
            closeModal()
        } else {
            setValidacao({ ...validacaoForm(funcionario) })
        }  
    }
    
    return <div className="editar">
            <p style={{ marginTop: '0px', fontSize: 26 }}>Cadastro</p>
            <div style={{ width : '100%', margin: '20px 0px', display: 'flex', justifyContent: 'space-between'}}>
                <TextField label="Nome" error={validacao.nome.error} helperText={validacao.nome.tipo}
                    variant="outlined" size="small" className={classes.input} onChange={e => {setFuncionario({...funcionario, nome : e.target.value})}}
                />
                <TextField label="Sobrenome" onChange={e => { setFuncionario({ ...funcionario, sobrenome: e.target.value }) }}
                variant="outlined" size="small" className={classes.input} error={validacao.sobrenome.error} helperText={validacao.sobrenome.tipo}
                />
            </div>
            <div style={{ width: '100%', margin: '20px 0px', display: 'flex', justifyContent: 'space-between' }}>
                <TextField label="Data de Nascimento" onChange={e => {setFuncionario({...funcionario, dtNascimento : e.target.value})}}
                variant="outlined" size="small" className={classes.input} error={validacao.dtNascimento.error} helperText={validacao.dtNascimento.tipo}
                />
                <TextField label="Cargo" onChange={e => {setFuncionario({...funcionario, cargo : e.target.value})}}
                variant="outlined" size="small" className={classes.input} error={validacao.cargo.error} helperText={validacao.cargo.tipo}
                />
            </div>
            <div style={{ margin: '20px 0px', display: 'flex', justifyContent: 'space-between' }}>
                <TextField label="Salário" onChange={e => {setFuncionario({...funcionario, salario : e.target.value})}}
                variant="outlined" size="small" className={classes.inputSalario} error={validacao.salario.error} helperText={validacao.salario.tipo}
                />
            </div>
            <div style={{ margin: '20px 0px', display: 'flex', justifyContent: 'space-between' }}>
                <TextField label="Descrição do Cargo" multiline 
                    rows={5} variant="outlined" className={classes.multiline} onChange={e => {setFuncionario({...funcionario, descricaoCargo: e.target.value})}}
                    error={validacao.descricaoCargo.error} helperText={validacao.descricaoCargo.tipo}
                />
            </div>
            <Button variant="contained" color="primary" onClick={()=> {addFirebase()}}
                    style={{ marginRight: '10px', backgroundImage: 'linear-gradient(90deg, #2fcf24 0%, #10640a 100%)' }}>
                Cadastrar 
            </Button>                
      </div>;
}

export default CadastroFuncionarios;