import React, { useEffect , memo} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TablePagination from '@material-ui/core/TablePagination';
import Fab from '@material-ui/core/Fab'
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import TableRow from '@material-ui/core/TableRow';
import { requestFirebase } from '../util/firebase/RequestFirebase'
import { LocalStorage } from '../util/localstorage/LocalStorage'
import { useFuncionarios } from '../context/Funcionarios'
import ModalEdicao from './EdicaoFuncionarios/ModalEdicao'

const columns = [
    { id: 'nome', label: 'Nome', minWidth: 170 },
    { id: 'dtNascimento', label: 'Data de Nascimento', minWidth: 100 },
    { id: 'cargo', label: 'Cargo', minWidth: 100 },
    { id: 'salario', label: 'Salário', minWidth: 100 },
    { id: 'editar', label: 'Editar', maxWidth: 80 },
    { id: 'deletar', label: '', maxWidth: 80 },
];

const useStyles = makeStyles({
    root: {
        width: '100%',
    },
    container: {
        maxHeight: 440,
    },
});

function ListagemFuncionarios() {
    const classes = useStyles();
    const { funcionarios, setFuncionarios } = useFuncionarios()
    const [ page, setPage] = React.useState(0);
    const [ rowsPerPage, setRowsPerPage] = React.useState(10);
    const [ rows, setRows ] = React.useState([]);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };   

    useEffect(() => {  
        const createData = (nome, dtNascimento, cargo, salario, key) => {
            let editar = <ModalEdicao keyUnq={key}/>
            let deletar = <Fab size="small" color="secondary" aria-label="edit" onClick={() => deletarFirebase(key)}
                style={{ backgroundImage: 'linear-gradient(90deg, #c21616 0%, #630c0c 100%)' }}><DeleteForeverIcon /></Fab>

            return { nome, dtNascimento, cargo, salario, editar, deletar, key };
        }

        const deletarFirebase = (key) => {
            requestFirebase.del(key)

            let aux = funcionarios
            delete aux[key]
            setFuncionarios({...aux})
        }
        
        const attRows = (funcionarios, createData) => {
            let auxArray = Object.values(funcionarios).map((element, index) =>
                createData(`${element.nome} ${element.sobrenome}`, element.dtNascimento, element.cargo, element.salario, Object.keys(funcionarios)[index])
            )

            return auxArray
        }

        if (funcionarios){
            setRows(attRows(funcionarios, createData))
            LocalStorage.addLocalStorage('funcionarios', funcionarios)
        }

    },[funcionarios, setFuncionarios])

    return (
        <Paper className={classes.root}>
            <TableContainer className={classes.container}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                        <TableRow>
                            {columns.map((column) => (
                                <TableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{ minWidth: column.minWidth }}
                                >
                                    <b>{column.label}</b> 
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                            return (
                                <TableRow hover role="checkbox" tabIndex={-1} key={row.key}>
                                    {columns.map((column) => {
                                        const value = row[column.id];
                                        return (
                                            <TableCell key={column.id} align={column.align}>
                                                {column.format && typeof value === 'number' ? column.format(value) : value}
                                            </TableCell>
                                        );
                                    })}
                                </TableRow>
                            );
                        })}
                    </TableBody>
                </Table>
            </TableContainer>
            <TablePagination
                rowsPerPageOptions={[10, 25, 100]}
                component="div"
                count={rows.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
            />
        </Paper>
    );
}

export default memo(ListagemFuncionarios)