import React, { createContext, useState, useContext } from "react";

export const FuncionariosContext = createContext();

export default function FuncionarioProvider({ children }) {
    const [funcionarios, setFuncionarios] = useState(null)

    return <FuncionariosContext.Provider value={{
        funcionarios,
        setFuncionarios,
    }}>
        {children}
    </FuncionariosContext.Provider>

}

export function useFuncionarios() {
    const context = useContext(FuncionariosContext)
    const { funcionarios , setFuncionarios } = context;
    return { funcionarios, setFuncionarios }
}