import React, { useState } from 'react';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import './style.css'
import { requestFirebase } from '../../util/firebase/RequestFirebase'
import { useFuncionarios } from '../../context/Funcionarios'
import validacaoForm, { temp } from '../../util/validacaoForm/validacaoForm'

const useStyles = makeStyles((theme) => ({
    input: { width: '48%' },
    inputSalario: { width: '48%' },
    multiline: { width: '100%' },
}));

function EdicaoFuncionarios({keyUnq , closeModal}) {    
    const { funcionarios, setFuncionarios } = useFuncionarios()   
    const [ funcionario, setFuncionario ] = useState(funcionarios[keyUnq])
    const [ validacao, setValidacao ] = useState(validacaoForm(funcionario))

    const classes = useStyles();

    const update = async () => {    
        if (Object.values(validacaoForm(funcionario)).every(e => e.error === false)){
            await requestFirebase.addOrUpdate(keyUnq, funcionario)
            setFuncionarios({ ...funcionarios, [keyUnq]: funcionario })
            setValidacao({ ...temp })
            closeModal()
        } else {
            setValidacao({ ...validacaoForm(funcionario) })
        }        
    }

    return <div className="cadastro">
        <p style={{marginTop:'0px', fontSize: 26}}>Editar</p>
        <div style={{ width: '100%', margin: '20px 0px', display: 'flex', justifyContent: 'space-between' }}>
            <TextField label="Nome" defaultValue={funcionario.nome} id="nome" error={validacao.nome.error} helperText={validacao.nome.tipo}
                variant="outlined" size="small" className={classes.input} onChange={e => { setFuncionario({ ...funcionario, nome: e.target.value }) }}
            />
            <TextField label="Sobrenome" onChange={e => { setFuncionario({ ...funcionario, sobrenome: e.target.value }) }}
                variant="outlined" size="small" className={classes.input} defaultValue={funcionario.sobrenome} 
                error={validacao.sobrenome.error} helperText={validacao.sobrenome.tipo}
            />
        </div>
        <div style={{ width: '100%', margin: '20px 0px', display: 'flex', justifyContent: 'space-between' }}>
            <TextField label="Data de Nascimento" onChange={e => { setFuncionario({ ...funcionario, dtNascimento: e.target.value }) }}
                variant="outlined" size="small" className={classes.input} defaultValue={funcionario.dtNascimento} 
                error={validacao.dtNascimento.error} helperText={validacao.dtNascimento.tipo}
            />
            <TextField label="Cargo" onChange={e => { setFuncionario({ ...funcionario, cargo: e.target.value }) }}
                variant="outlined" size="small" className={classes.input} defaultValue={funcionario.cargo}
                error={validacao.cargo.error} helperText={validacao.cargo.tipo}
            />
        </div>
        <div style={{ margin: '20px 0px', display: 'flex', justifyContent: 'space-between' }}>
            <TextField label="Salário" onChange={e => { setFuncionario({ ...funcionario, salario: e.target.value }) }}
                variant="outlined" size="small" className={classes.inputSalario} defaultValue={funcionario.salario}
                error={validacao.salario.error} helperText={validacao.salario.tipo}
            />
        </div>
        <div style={{ margin: '20px 0px', display: 'flex', justifyContent: 'space-between' }}>
            <TextField label="Descrição do Cargo" multiline defaultValue={funcionario.descricaoCargo}
                rows={5} variant="outlined" className={classes.multiline} onChange={e => { setFuncionario({ ...funcionario, descricaoCargo: e.target.value }) }}
                error={validacao.descricaoCargo.error} helperText={validacao.descricaoCargo.tipo}
            />
        </div>
        <Button variant="contained" color="primary" onClick={() => { update() }}
            style={{ marginRight: '10px', backgroundImage: 'linear-gradient(90deg, #2fcf24 0%, #10640a 100%)' }}>
            Editar
        </Button>
    </div>;
}

export default EdicaoFuncionarios;