export const temp = {
    nome : { error: false, tipo: "" },
    sobrenome : { error: false, tipo: "" },
    dtNascimento : { error: false, tipo: "" },
    cargo: { error: false, tipo: "" },
    salario: { error: false, tipo: "" },
    descricaoCargo: { error: false, tipo: "" }
}

export default function validacao(funcionario){
    let temp = {}

    temp.nome = funcionario.nome ? { error: false, tipo: "" } : { error: true, tipo: "Campo Obrigatório" }

    temp.sobrenome = funcionario.sobrenome ? { error: false, tipo: "" } : { error: true, tipo: "Campo Obrigatório" }

    temp.dtNascimento = (/^([0-2][0-9]|(3)[0-1])(\/)(((0)[0-9])|((1)[0-2]))(\/)\d{4}$/i).test(funcionario.dtNascimento)
     ? { error: false, tipo: "" } : { error: true, tipo: "Data não é valida" }

    temp.cargo = funcionario.cargo ? { error: false, tipo: "" } : { error: true, tipo: "Campo Obrigatório" }

    temp.salario = funcionario.salario ? { error: false, tipo: "" } : { error: true, tipo: "Campo Obrigatório" }

    temp.descricaoCargo = funcionario.descricaoCargo ? { error: false, tipo: "" } : { error: true, tipo: "Campo Obrigatório" }

    return temp
}